import { Validators } from '@angular/forms';
import { FormControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
import { SlotDetailService } from './../../../services/slot-detail.service';
import { StudService } from './../../../services/stud.service';
import { FormGroup } from '@angular/forms';
import { SlotDetails } from 'src/app/models/slotDetails';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AssignSlotService } from 'src/app/services/assign-slot.service';

@Component({
  selector: 'app-update-payment',
  templateUrl: './update-payment.component.html',
  styleUrls: ['./update-payment.component.css']
})
export class UpdatePaymentComponent implements OnInit {
  @Input("slot") slot: SlotDetails;
  @Input("studData") studData;
  @Output("onAllot") onAllot: EventEmitter<any> = new EventEmitter();
  form: FormGroup;
  studList: any[];
  constructor(
    private studService: StudService,
    private slotAssignService: AssignSlotService,
    private slotDetailService: SlotDetailService,
    private _snackBar:MatSnackBar
  ) {}

  ngOnInit() {
    this.createControls();
    this.getList();
  }
  isSelected() {
    return typeof this.slot !== "undefined";
  }
  getList() {
    this.studService.getUpdatedStudList().subscribe((studList) => {
      this.studList = studList;
    });
  }
  createControls() {
    this.form = new FormGroup({
      slotId: new FormControl(true, { validators: [] }),
      studId: new FormControl(null, { validators: [Validators.required] }),
      bookedDate: new FormControl(null, { validators: [Validators.required] }),
      bookTime: new FormControl(null, { validators: [Validators.required] }),
      endDate: new FormControl(null, { validators: [Validators.required] }),
      totalFees: new FormControl(null, {
        validators: [Validators.required],
      }),
      paidFees: new FormControl(null, { validators: [Validators.required] }),
      balance: new FormControl(true, { validators: [Validators.required] }),
      isBooked: new FormControl(null, { validators: [Validators.required] }),
    });
    this.patchInit();
  }

  patchInit(){
     this.form.patchValue({
      studId: this.slot.studId,
      balance: this.slot.isBooked ? this.slot.balance : 0,
    });
  }
  allotSlot() {
    let date: Date = new Date();

    let balance = this.slot.isBooked
      ? this.slot.balance -
        this.form.get("paidFees").value+
        this.form.get("totalFees").value
      : this.form.get("totalFees").value - this.form.get("paidFees").value;
    if (this.isSelected())
      this.form.patchValue({
        balance: balance,
        isBooked: true,
        slotId: this.slot.slotId,
        bookTime: date.toLocaleTimeString(),
       });
        console.log(this.form.controls);

       if(this.form.invalid) return ;
       const bookedDate=this.form.get("bookedDate").value._i.date;
       const endDate=this.form.get("endDate").value._i.date;
       const diffDays =  endDate- bookedDate;
       console.log(diffDays);
       if(balance<0)  {
        this.patchInit();
        return this.openSnackBar("We Don't except more money than fees!!")
       } ;
       if(diffDays<0)  return this.openSnackBar("You can book seat for at least  7 days!!") ;

      this.slotAssignService.assignSlot(this.form.value).subscribe((result) => {
      this.form.reset();
      this.slotDetailService.getAllSlotDetails();
      this.onAllot.emit(this.slot);
    });
  }

  openSnackBar(msg){
    this._snackBar.open(msg,"okay",{
      duration:3000,
      horizontalPosition:"center",
      verticalPosition:"top"
    })
  }
}
