import { CommentService } from './comment.service';
import { MatSnackBar } from '@angular/material';
import { Router } from "@angular/router";
import { Subject } from "rxjs";
import { HttpClient, HttpHeaders, HttpResponse } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from 'src/environments/environment';

const BACKEND_URL=environment.apiUrl+"auth/login"
@Injectable({
  providedIn: "root",
})
export class AuthService {
  isAuthenticated: boolean = false;
  tokenTimer;
  token:string="";
  authListner = new Subject<any>();
  constructor(private _snackBar:MatSnackBar,
              private http: HttpClient,
               private router: Router) {}

  getIsAuth() {
    return this.isAuthenticated;
  }

  getToken(){
    return this.token;
  }
  getAutoAuth() {
    return this.authListner.asObservable();
  }
  onLogin(loginCreds) {
    const httpOptions = {
      headers: new HttpHeaders({
        "Content-Type": "application/json",
      }),
      observe: "response" as "response",
    };
    this.http
      .post(BACKEND_URL, loginCreds)
      .subscribe(
        (isValid: { token; expiresIn }) => {
          if (isValid.token) {
          this.token=isValid.token;
            const expiresInDuration = isValid.expiresIn;
            this.isAuthenticated = true;
            const now = new Date();
            const expiresInDate = new Date(
              now.getTime() + expiresInDuration * 1000
            );
            this.setAuthTimer(expiresInDuration);
            this.authListner.next(true);
            this.saveAuth(isValid.token, expiresInDate);
            this.router.navigateByUrl("/");
          } else console.log(isValid);
        }
      );
  }

  autoAuthAdmin() {
    const authInfo = this.getAuthData();
    if (!authInfo) return;
    const now = new Date();
    const expiresIn = authInfo.expirationDate.getTime() - now.getTime();
    if (expiresIn > 0) {
      this.isAuthenticated = true;
      this.setAuthTimer(expiresIn / 1000);
      this.authListner.next(true);
      this.token = authInfo.token;
    }else{
      this.logout();
    }


  }

  private getAuthData() {
    const token = localStorage.getItem("token");
    const expiration = localStorage.getItem("expiration");
    if (!token || !expiration) return;
    return {
      token: token,
      expirationDate: new Date(expiration),
    };
  }


  private setAuthTimer(expiresInDuration) {
    this.tokenTimer = setTimeout(() => {
      this.logout();
    }, expiresInDuration * 1000);
  }


  saveAuth(token, expirationDate) {
    localStorage.setItem("token", token);
    localStorage.setItem("expiration", expirationDate.toISOString());
  }

  logout() {
    this.clearAuth();
    this.isAuthenticated=false;
    this.authListner.next(false);
    this.token='';
    this.tokenTimer=null;
    this.router.navigate(["/login"]);
  }

  clearAuth() {
    localStorage.removeItem("token");
    localStorage.removeItem("expiration");
  }
}
