import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { MatSnackBar } from "@angular/material";
import { mimeType } from "./mimeType-validators";
import { StateDistService } from "./../../resources/state-dist.service";
import { Component, OnDestroy, OnInit } from "@angular/core";
import { FormControl, FormGroup, NgForm, Validators } from "@angular/forms";
import { StudyCenterService } from 'src/app/services/study-center.service';

@Component({
  selector: "app-add-update",
  templateUrl: "./add-update.component.html",
  styleUrls: ["./add-update.component.css"],
})
export class AddUpdateComponent implements OnInit,OnDestroy {
  allStateDist = [];
  allDists = [];
  form: FormGroup;
  imagePreview: String;
  centerId: String;
  centerSubscription:Subscription;
  updateSubscription:Subscription;
  btnName="Add";
  constructor(
    private stateDist: StateDistService,
    private studyCenter: StudyCenterService,
    private _snackBar: MatSnackBar,
    private router:Router
  ) {}

  ngOnInit() {
    this.btnName="Add"
    this.allStateDist = this.stateDist.getStates();
    this.form = new FormGroup({
      title: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType],
      }),
      address: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      pincode: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      email: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      mobileno: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      totalSlots: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(1)],
      }),
      area: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      city: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      district: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      state: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
    });
    this.getCenter();
  }

  stateSelected(stateName) {
    for (let i = 0; i < this.allStateDist.length; i++) {
      if (this.allStateDist[i].name == stateName) {
        this.allDists = this.allStateDist[i].districts;
      }
    }
  }

  openSnackBar(msg: string) {
    this._snackBar.open(msg, "Okay", {
      duration: 1000,
      horizontalPosition: "center",
      verticalPosition: "top",
    });
  }

  getCenter() {
    this.btnName="Update"
    this.centerSubscription=this.studyCenter
      .getCenter()
      .subscribe((centerDetails: { result: any[] }) => {
        if (centerDetails.result.length > 0) {
          let cd = centerDetails.result[0];
          this.centerId = cd._id;
          this.stateSelected(cd.state);
          this.imagePreview = cd.image;
          this.form.patchValue({
            ...cd,
            image: null
          });
        }
      });
  }

  addUpdateStudyCenter() {
    if (this.form.valid) {
      this.updateSubscription=this.studyCenter
        .addUpdateStudyCenter(this.form.value, this.centerId)
        .subscribe((res:{msg:string}) => {
          this.openSnackBar(res.msg);
          this.router.navigateByUrl("");
        });
    }
  }

  onImagePicked($event: HTMLInputElement) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.form.get("image").updateValueAndValidity();
    const reader = new FileReader();
    reader.onload = () => {
      this.imagePreview = reader.result as String;
    };
    if (file) reader.readAsDataURL(file);
  }

  ngOnDestroy(){
    this.centerSubscription.unsubscribe();
    if(typeof this.updateSubscription!=='undefined')
      this.updateSubscription.unsubscribe();
  }

}
