import { Subscription } from 'rxjs';
import { SanckBar } from 'src/app/services/snack-popup.service';
import { AuthService } from './../../../services/auth.service';
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { FacilityService } from "./../../../services/facility.service";
import { Component, OnDestroy, OnInit } from "@angular/core";

@Component({
  selector: "app-viewfac",
  templateUrl: "./viewfac.component.html",
  styleUrls: ["./viewfac.component.css"],
})
export class ViewfacComponent implements OnInit,OnDestroy {
  isAdmin:boolean=false;
  isLoading=true;
  facilities: any[]=[];
  facilitiesSubscription:Subscription;
  constructor(private facService: FacilityService,
              private authService:AuthService,
              private router:Router,
              private _snackBar:SanckBar) {}

  ngOnInit() {
    this.facService.getFacilities();
    this.isAdmin=this.authService.getIsAuth();
    this.authService.getAutoAuth().subscribe(isAdmin=>{
      this.isAdmin=isAdmin;
    });

    this.facilitiesSubscription=this.facService.facilitiesSubjectListener.subscribe(
      (obs) => {
        this.facilities=obs.faclities;
        this.isLoading=false;
      }
    );
  }


  toggle(fac){
    var facs=document.getElementsByClassName("all");
    for(var i=0;i<facs.length;i++){
      if(fac===i) facs[fac].setAttribute('style','display:block');
      else facs[i].setAttribute('style','display:none');
    }
  }

  deleteFac(facId:string){
    this.facService.deleteFacility(facId).subscribe((result:{msg:string})=>{
      this.facService.getFacilities();
      this._snackBar.openSnack(result.msg);
    });
  }

  ngOnDestroy(){
    this.facilitiesSubscription.unsubscribe();
  }
}
