import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { SlotsService } from './../../services/slots.service';
import { Validators } from '@angular/forms';
import { FormControl } from '@angular/forms';
import { FormGroup } from '@angular/forms';
import { Component, OnInit, EventEmitter, Output, OnDestroy } from '@angular/core';
import { SanckBar } from 'src/app/services/snack-popup.service';

@Component({
  selector: 'app-add-slots',
  templateUrl: './add-slots.component.html',
  styleUrls: ['./add-slots.component.css']
})
export class AddSlotsComponent implements OnInit {
  form:FormGroup;
  @Output("slotAdded") slotAdded:EventEmitter<any> =new EventEmitter(true);
  constructor(private slotService:SlotsService,
              private snackBar:SanckBar) { }
  ngOnInit() {
    this.form=new FormGroup({
      _id:new FormControl(null),
      totalSlots:new FormControl(null,{validators:[Validators.required,Validators.min(0),Validators.max(300)]})
    });
  }
  addTotalSlots(){
     this.slotService.addToSlots(this.form.value).subscribe((result:{msg:string})=>{
     this.snackBar.openSnack(result.msg)
      this.slotAdded.emit();
    })
  }

}
