import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { environment } from 'src/environments/environment';

const BACKEND_URL=environment.apiUrl+"facilities";
@Injectable({
  providedIn:"root"
})

export class FacilityService{

  facilitiesSubjectListener=new Subject<{faclities:any[]}>();
  constructor(private http:HttpClient){}

  getFacilities(){
    this.http.get(BACKEND_URL)
    .subscribe((result:any[])=>{
      this.facilitiesSubjectListener.next({faclities:result})
    });
  }

  deleteFacility(facId:string){
    return this.http.delete(BACKEND_URL,{params:{facId:facId}});
  }
  getFacilitiesSubject(){
    return this.facilitiesSubjectListener.asObservable();
  }
  addFacilities(facData) {
    const formData =this.getFormData(facData);
    return this.http
      .post(BACKEND_URL, formData)

  }

  updateFacility(facData,facId:string,oldImgPath){

    let arrfileName=oldImgPath.split("images/");
    const formData =this.getFormData(facData);
    formData.append("_id", facId);
    formData.append("oldImgPath",arrfileName[1]);
    this.http.put(BACKEND_URL,formData).subscribe(res=>{
      console.log(res);
    });
  }

  getFormData(facData){
    const formData = new FormData();
    formData.append("centerId", facData.centerId);
    formData.append("title", facData.title);
    formData.append("image", facData.image, facData.title);
    formData.append("subtitle", facData.subtitle);
    formData.append("description", facData.description);
    return formData;
  }
}
