import { environment } from './../../environments/environment';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TransactionData } from '../models/transactionModel';

const BACKEND_URL=environment.apiUrl+"transactions"
@Injectable({
  providedIn: 'root'
})
export class TransactionService {

  transactionListner=new Subject<TransactionData>();
  constructor(private http:HttpClient) {

   }
   getStudSlotTransactions(Ids){
     this.http.get(BACKEND_URL,{params:Ids})
    .subscribe((TransactionData:TransactionData)=>{
         this.transactionListner.next(TransactionData);
      })
  }
   getUpdatedStudSlotTransactions(){
     return this.transactionListner.asObservable();
   }
}
