import { StudAuthService } from './../services/stud-auth.service';
import { AuthService } from './../services/auth.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  isAdmin=false;
  isStud=false;
  constructor(private authService:AuthService,private studAuthService:StudAuthService) { }

  ngOnInit() {
    this.isAdmin=this.authService.getIsAuth();
    this.authService.getAutoAuth().subscribe(isAuth=>{
      this.isAdmin=isAuth
    });

    this.isStud=this.studAuthService.isStudAuthenticated();
    this.studAuthService.isStudAuthUpdated()
    .subscribe(isStudAuth=>{
                this.isStud=isStudAuth;
            }
      )
  }

  logout(){
    if(this.isAdmin) this.authService.logout();
    else if(this.isStud) this.studAuthService.logout();
  }
}
