import { StudAuthService } from './../../services/stud-auth.service';
import { AuthService } from './../../services/auth.service';
import { Validators } from '@angular/forms';
import { FormControl } from '@angular/forms';
import { FormGroup } from '@angular/forms';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
isLoding=false;
form:FormGroup;
loginType=true;
  constructor(private authService:AuthService,private studAuthService:StudAuthService) { }
  ngOnInit() {
    this.form=new FormGroup({
      email:new FormControl(null,{validators:[Validators.email,Validators.required]}),
      password:new FormControl(null,{validators:[Validators.required,Validators.minLength(4)]})
    });
  }

  onSubmit(){
    this.isLoding=true;
    if(this.form.invalid) return;
    if(!this.loginType) this.authService.onLogin(this.form.value);
    else if(this.loginType) this.studAuthService.onLogin(this.form.value);
    this.form.reset();
    setTimeout(()=>{
      this.isLoding=false;
    },500)
  }
}
