import { MatSnackBar } from '@angular/material';
import { Subject } from 'rxjs';
import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import { environment } from 'src/environments/environment';
import { SanckBar } from './snack-popup.service';

const BACKEND_URL=environment.apiUrl+"slotDetails"
@Injectable({
  providedIn: 'root'
})
export class SlotDetailService {

 private allSlotListener=new Subject<{allSlots:[]}>();
  constructor(private http:HttpClient,private snack:SanckBar) { }
  addBookingDetails(slotDetails){
    return this.http.put(BACKEND_URL,slotDetails);
  }
  deleteSlot(slotId){
     this.http.delete(BACKEND_URL+"/"+slotId)
     .subscribe((result:{msg:string})=>{
      this.getAllSlotDetails();
      this.snack.openSnack(result.msg)
    })
  }
  getAllSlotDetails(){
     this.http.get(BACKEND_URL)
    .subscribe((result:{allSlots:[]})=>{
      this.allSlotListener.next(result);
    },err=>console.log(err))
  }

  getAllSlotList(){
    return this.allSlotListener.asObservable();
  }


  getStudSlot(studId){
   return  this.http.get(BACKEND_URL+"/"+studId)
   }

   getUnAssigned(){
    return this.http.get(BACKEND_URL+"/filter");
   }

  addExtraSlot(slotId){
    this.http.post(BACKEND_URL+"/newSlot",{slotId:slotId}).subscribe(res=>{
      this.getAllSlotDetails();
    })
  }
 }
