import { SlotDetailService } from './../../services/slot-detail.service';
import { Router } from '@angular/router';
import { SlotDetails } from './../../models/slotDetails';
import { StudService } from './../../services/stud.service';
import { AssignSlotService } from './../../services/assign-slot.service';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-view-allotted',
  templateUrl: './view-allotted.component.html',
  styleUrls: ['./view-allotted.component.css']
})
export class ViewAllottedComponent implements OnInit {
  @Input("selectedSlot") selectedSlot:SlotDetails;
  @Input("remDays") remDays;
  @Input("studData") studData;
  @Output("onStudSelect") onStudSelect:EventEmitter<any>=new EventEmitter();
  isAdmin=false;
  isLoading=true;
  constructor(private router:Router,private slotDetailService:SlotDetailService,
    private authAdminService:AuthService) {}
  ngOnInit() {
    this.isAdmin=this.authAdminService.getIsAuth();
    this.isLoading=false;
    }
  isLoaded(){
    return typeof this.studData!=="undefined";
  }
  onUpdate(){
    this.onStudSelect.emit();
  }
  onDelete(){
    this.slotDetailService.deleteSlot(this.selectedSlot._id);
  }

  redTransaction(){
     this.selectedSlot.studOId=this.selectedSlot.studId._id;
    this.router.navigate(["/transactions"],{queryParams:this.selectedSlot})
  }
}
