import { map } from "rxjs/operators";
import { Observable, Subscription } from "rxjs";
import { combineLatest } from "rxjs";
import { StudService } from "./../../services/stud.service";
import { FormGroup, FormControl } from "@angular/forms";
import { Validators } from "@angular/forms";
import { CommentService } from "./../../services/comment.service";
import { AuthService } from "src/app/services/auth.service";
import { StudAuthService } from "./../../services/stud-auth.service";
import { Component, OnDestroy, OnInit } from "@angular/core";

@Component({
  selector: "app-comments",
  templateUrl: "./comments.component.html",
  styleUrls: ["./comments.component.css"],
})
export class CommentsComponent implements OnInit, OnDestroy{
  isStudAuthed = false;
  isAdmin = false;
  form: FormGroup;
  commentSubscription:Subscription;
  studData;
  studId;
  comments = [];
  constructor(
    private studAuthService: StudAuthService,
    private studService: StudService,
    private adminAuthService: AuthService,
    private commentService: CommentService
  ) {}

  ngOnInit() {
    this.isStudAuthed = this.studAuthService.isStudAuthenticated();
    this.commentService.getComments();
    this.getComments();
    this.studAuthService
      .isStudAuthUpdated()
      .subscribe((isAuthed) => (this.isStudAuthed = isAuthed));
    this.isAdmin = this.adminAuthService.getIsAuth();
    this.adminAuthService
      .getAutoAuth()
      .subscribe((isAuthed) => (this.isAdmin = isAuthed));
    this.createContols();
  }

  createContols() {
    if (!this.isStudAuthed) return;
    this.form = new FormGroup({
      comment: new FormControl(null, { validators: [Validators.required] }),
      commentDate: new FormControl(null, { validators: [Validators.required] }),
      studId: new FormControl(null, { validators: [Validators.required] }),
    });
    this.patchValues();
  }

  patchValues() {
    this.studId = localStorage.getItem("studId");
    this.form.patchValue({ studId: this.studId, commentDate: Date.now() });
    this.studService.getStudent(this.studId).subscribe((studData) => {
      this.studData = studData;
    });
  }
  
  getComments() {
    this.commentSubscription=this.commentService.getUpdatedComments().subscribe((comments: []) => {
      this.comments = comments;
    });
  }

  createComments() {
    this.commentService.createComment(this.form.value);
    this.resetForm();
  }

  resetForm(){
    this.form.reset();
    this.form.markAsPristine();
    this.form.markAsUntouched();
    this.patchValues();
  }

  toggleComment(commentId, isValid) {
    this.commentService.toggleComment(commentId, isValid);
  }

  deleteComment(commentId) {
    this.commentService.deleteComment(commentId);
  }

  ngOnDestroy(){
    this.commentSubscription.unsubscribe();
  }

}
