import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

const BACKEND_URL=environment.apiUrl+"slots"
@Injectable({
  providedIn: 'root'
})
export class SlotsService {

  constructor(private http:HttpClient) { }

  addToSlots(totalSlots){
     return this.http.post(BACKEND_URL,totalSlots);
  }

  getTotalSlots(){
      return this.http.get(BACKEND_URL);
  }

  updateToSlots(totalSlots){
    return this.http.put(BACKEND_URL,totalSlots);
  }

  deleteToSlots(slotInfo){
    return this.http.delete(BACKEND_URL,slotInfo);
  }
}
