import { SanckBar } from 'src/app/services/snack-popup.service';
import { MomentDateAdapter } from "@angular/material-moment-adapter";
import { Router } from "@angular/router";
import { MatSnackBar } from "@angular/material";
import { AssignSlotService } from "./../../services/assign-slot.service";
import { StudService } from "./../../services/stud.service";
import { SlotDetailService } from "./../../services/slot-detail.service";
import { Validators, FormControl, FormGroup } from "@angular/forms";
import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from "@angular/core";
import { SlotDetails } from "src/app/models/slotDetails";
import { flatMap, map, switchMap } from "rxjs/operators";
import { Observable, interval, Subscription } from "rxjs";
@Component({
  selector: "app-assign-slot",
  templateUrl: "./assign-slot.component.html",
  styleUrls: ["./assign-slot.component.css"],
})
export class AssignSlotComponent implements OnInit, OnDestroy {
  @Input("slot") slot: SlotDetails;
  @Input("studData") studData;
  @Output("onAllot") onAllot: EventEmitter<any> = new EventEmitter();

  form: FormGroup;
  studList: any[] = [];
  currentDate = Date.now();
  listSubscription: Subscription;
  isLoading=true;

  constructor(
    private slotAssignService: AssignSlotService,
    private slotDetailService: SlotDetailService,
    private _snackBar: SanckBar,
    private router: Router
  ) {}

  myFilter = (d): boolean => {
    let bDate = this.form.get("bookedDate").value._i;
    return d._i.year > bDate.year
      ? true
      : d._i.year < bDate.year
      ? false
      : d._i.month > bDate.month
      ? true
      : d._i.month < bDate.month
      ? false
      : d._i.date - 5 > bDate.date;
  };

  ngOnInit() {
    this.createControls();
    this.getList();
  }
  isSelected() {
    return typeof this.slot !== "undefined";
  }

  getList() {
    this.listSubscription = this.slotDetailService
      .getUnAssigned()
      .subscribe((unAssignedList: { slotList: [] }) => {
        this.studList = unAssignedList.slotList;
        this.isLoading=false;
      });
  }

  createControls() {
    this.form = new FormGroup({
      slotId: new FormControl(true, { validators: [] }),
      studId: new FormControl(null, { validators: [Validators.required] }),
      bookedDate: new FormControl(null, { validators: [Validators.required] }),
      endDate: new FormControl(null, { validators: [Validators.required] }),
      totalFees: new FormControl(null, {
        validators: [Validators.required],
      }),
      paidFees: new FormControl(null, { validators: [Validators.required] }),
      balance: new FormControl(true, { validators: [Validators.required] }),
    });
    this.patchInit();
  }

  patchInit() {
    this.form.patchValue({
      studId: this.slot.studId,
      balance: this.slot.isBooked ? this.slot.balance : 0,
    });
  }
  allotSlot() {
    this.isLoading=true;
    let date: Date = new Date();
    const bookTime = new FormControl(null, {
      validators: [Validators.required],
    });
    const isBooked = new FormControl(null, {
      validators: [Validators.required],
    });
    this.form.addControl("bookTime", bookTime);
    this.form.addControl("isBooked", isBooked);
    let balance = this.slot.isBooked
      ? this.slot.balance -
        this.form.get("paidFees").value +
        this.form.get("totalFees").value
      : this.form.get("totalFees").value - this.form.get("paidFees").value;
    if (this.isSelected())
      this.form.patchValue({
        balance: balance,
        isBooked: true,
        slotId: this.slot.slotId,
        bookTime: date.toLocaleTimeString(),
      });

    if (this.form.invalid) return this.isLoading=false;

    if (balance < 0) {
      this.patchInit();
      this.isLoading=false;
      return this._snackBar.openSnack("We Don't except more money than fees!!");
    }
    this.slotAssignService.assignSlot(this.form.value)
    .subscribe(() => {
      this.slotDetailService.getAllSlotDetails();
      this.isLoading=false;
      this.onAllot.emit();
      this.form.reset();
    });
  }

  redTransaction() {
    this.router.navigate(["/transactions"], { queryParams: this.slot });
  }

  ngOnDestroy() {
    this.listSubscription.unsubscribe();
  }
}
