import { ActivatedRoute } from "@angular/router";
import { StudService } from "./../../services/stud.service";
import { SlotDetailService } from "./../../services/slot-detail.service";
import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  OnDestroy,
} from "@angular/core";
import { Subscription } from "rxjs";

@Component({
  selector: "app-view-slots",
  templateUrl: "./view-slots.component.html",
  styleUrls: ["./view-slots.component.css"],
})
export class ViewSlotsComponent implements OnInit, OnDestroy {
  selectedSlot;
  remDays;
  curDate = new Date();
  endDate;
  isUpdating = false;
  isLoading = true;
  slotList: []=[];
  studData;
  slotSubscription: Subscription;
  i: number = 0;
  constructor(
    private slotsDetailService: SlotDetailService,
  ) {}

  ngOnInit() {
    this.getSlots();
  }

  addExtraSlot() {
    this.slotsDetailService.addExtraSlot(this.slotList.length + 1);
  }

  getSlots() {
    this.slotsDetailService.getAllSlotDetails();
    this.slotSubscription = this.slotsDetailService
      .getAllSlotList()
      .subscribe((obs) => {
        this.isLoading=false;
        if (!(obs.allSlots.length > 0)) return;
        this.slotList = obs.allSlots;
        this.selectedSlot = obs.allSlots[this.i];
        this.getStud();
      });
  }

  onStudSelect() {
    this.isUpdating = true;
  }
  getStud() {
    if ( typeof this.selectedSlot.studId === "undefined") return;
     this.studData=this.selectedSlot.studId;
     this.isLoading = false;
          var diff = Math.abs(this.curDate.getTime() - new Date(this.selectedSlot.endDate.toString()).getTime());
          this.remDays = Math.ceil(diff / (1000 * 3600 * 24));
  }

  isSelected() {
    return typeof this.selectedSlot !== "undefined";
  }

  onAllot() {
    this.isUpdating = false;
  }

  onSlotSelected(slot) {
    this.i = slot.slotId - 1;
    this.selectedSlot = slot;
    this.isUpdating = false;
    this.getStud();
  }

  ngOnDestroy() {
    this.slotSubscription.unsubscribe();
  }
  
}
