import { Subscription } from "rxjs";
import { StudService } from "./../../services/stud.service";
import { Component, OnDestroy, OnInit } from "@angular/core";

@Component({
  selector: "app-profile",
  templateUrl: "./profile.component.html",
  styleUrls: ["./profile.component.css"],
})
export class ProfileComponent implements OnInit, OnDestroy {
  studData;
  studSubscription: Subscription;
  constructor(private studService: StudService) {}

  ngOnInit() {
    this.studSubscription = this.studService
      .getStudent(localStorage.getItem("studId"))
      .subscribe((stud) => {
        this.studData = stud;
      });
  }

  ngOnDestroy() {
    this.studSubscription.unsubscribe();
  }
}
