import { Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { StudService } from './../../services/stud.service';
import { Component, OnInit, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-viewstud',
  templateUrl: './viewstud.component.html',
  styleUrls: ['./viewstud.component.css']
})
export class ViewstudComponent implements OnInit,OnDestroy {
  studList:any[];
  isLoading=true;
  studListSubscription:Subscription;
  constructor(private studService:StudService) { }

  ngOnInit() {
    this.studListSubscription=this.studService.getUpdatedStudList().subscribe(result=>{
      this.studList=result;
      this.isLoading=false;
    })
  }
  deleteStud(studId){
    this.studService.deleteStud(studId);
  }

  ngOnDestroy(){
    this.studListSubscription.unsubscribe();
  }

}
