import { NavigationEnd, Router, ActivatedRoute } from '@angular/router';
import { AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  isLoading=true;
  constructor(private activatedRoute:ActivatedRoute) {this.scrollToFacility()}
  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(isExist=>{
      if(isExist.lebel) setTimeout(()=>{
        this.scrollToFacility();
      },1000)
    })
    this.isLoading=false;
    }


  private scrollToFacility(){
    const element = document.getElementById('facilities');
    if(element) element.scrollIntoView();
  }
}
