import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
const BACKEND_URL=environment.apiUrl+"student";
@Injectable({
  providedIn: 'root'
})
export class StudAuthService {
  isStudAuthListner=new Subject<any>();
  private token='';
  tokenTimer='';
  studId:string='';
  isStudAuthed=false;
  constructor(private http:HttpClient,private _snackBar:MatSnackBar
    ,private router:Router) { }

    isStudAuthenticated(){
      return this.isStudAuthed;
    }
    getStudId(){
      return this.studId;
    }
    studToken(){
      return this.token;
    }
    isStudAuthUpdated(){
      return this.isStudAuthListner.asObservable();
    }

  onLogin(loginCreds){
    this.http.post(BACKEND_URL+"/auth",loginCreds)
    .subscribe((isValid:{token:string,id:string,expiresIn:number})=>{
      if(isValid.token){
        this.studId=isValid.id;
        this.token=isValid.token;
        this.isStudAuthed=true;
        this.isStudAuthListner.next(true);
        let now=new Date();
        let expiresInDate=new Date(now.getTime()+isValid.expiresIn*1000);
        this.setTimer(isValid.expiresIn);
        this.saveStudData(isValid.token,expiresInDate);
        this.router.navigateByUrl("/");
      }else {
        this.openSnack();
      }
    })
  }

  autoAuthStud(){
    let studData=this.getStudAuthData();
    if(!studData.token) return;
    this.studId=studData.studId;
    const now =new Date();
    const expiresIn = studData.expirationDate.getTime() - now.getTime();
       if (expiresIn > 0) {
        this.isStudAuthListner.next(true);
        this.token = studData.token;
        this.isStudAuthed = true;
        this.setTimer(expiresIn / 1000);
      }else{
        this.logout();
      }
  }

  saveStudData(token,expirationDate){
    localStorage.setItem("studId",this.studId);
    localStorage.setItem("studToken",token);
    localStorage.setItem("studExpiresIn",expirationDate);
  }

  setTimer(expiresIn){
    setTimeout(()=>{
      this.logout();
    },expiresIn*1000)
  }

  getStudAuthData(){
    return {studId:localStorage.getItem("studId"),
            token:localStorage.getItem("studToken"),
            expirationDate:new Date(localStorage.getItem("studExpiresIn"))};
  }

  logout(){
    this.isStudAuthListner.next(false);
    localStorage.removeItem("studId");
    localStorage.removeItem("studToken");
    localStorage.removeItem("studExpiresIn");
    this.isStudAuthed=false;
    this.token='';
    this.router.navigateByUrl("/");
  }
  openSnack(){
    this._snackBar.open("Invalid login details","okay",{
      duration:3000,
      horizontalPosition:"center",
      verticalPosition:"top"
    })
  }
}
