import { Subject } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { MatSnackBar } from "@angular/material";
import { environment } from 'src/environments/environment';
const BACKEND_URL=environment.apiUrl+"student";

@Injectable({
  providedIn: "root",
})
export class StudService {
  studListListener = new Subject<any[]>();
  constructor(private http: HttpClient, private _snackBar: MatSnackBar) {}

  addStudent(studData) {
    this.getAllStudents();
    return this.http.post(
      BACKEND_URL,
      this.appendData(studData)
    );
  }

  updateStudent(studData, studId,oldImgPath) {
    let newStudData=this.appendData(studData);
        newStudData.append("oldImgPath",oldImgPath.split("images/")[1]);
    return this.http.put(
      BACKEND_URL+"/" + studId,
      newStudData
    );
  }
  getStudent(studId) {
    return this.http.get(BACKEND_URL+"/" + studId);
  }

  getAllStudents() {
     this.http
      .get(BACKEND_URL)
      .subscribe((result: []) => {
        this.studListListener.next(result);
      });
  }
  getUpdatedStudList() {
    this.getAllStudents();
    return this.studListListener.asObservable();
  }

  deleteStud(studId) {
    this.http.delete(BACKEND_URL+"/" + studId).subscribe(
      (resp:{msg:string}) => this.openSnackBar(resp.msg)
    );
  }
  appendData(studData): FormData {
    const formData = new FormData();
    formData.append("name", studData.name);
    formData.append("password", studData.password);
    formData.append("mobile", studData.mobile);
    formData.append("email", studData.email);
    formData.append("dob", studData.dob);
    formData.append("qualification", studData.qualification);
    formData.append("marrageStatus", studData.marrageStatus);
    formData.append("gender", studData.gender);
    formData.append("image", studData.image, studData.name);
    return formData;
  }

  openSnackBar(msg) {
    this.getAllStudents();
    this._snackBar.open(msg, "Okay", {
      duration: 2000,
      horizontalPosition: "center",
      verticalPosition: "top",
    });
  }
}
