import { StudslotComponent } from './student/studslot/studslot.component';
import { StudGuard } from './auth/stud.guard';
import { ProfileComponent } from './student/profile/profile.component';
import { Authguard } from './auth/auth.guard';
import { LoginComponent } from './auth/login/login.component';
import { ViewTransactionsComponent } from './admin/view-transactions/view-transactions.component';
import { ViewSlotsComponent } from './admin/view-slots/view-slots.component';
import { AddSlotsComponent } from './admin/add-slots/add-slots.component';
import { StudSignupComponent } from './student/stud-signup/stud-signup.component';
import { AddUpdatefacsComponent } from './admin/facilities/add-updatefacs/add-updatefacs.component';
import { HomeComponent } from './home/home.component';
import { AddUpdateComponent } from './admin/add-update/add-update.component';
import { NgModule, Component } from '@angular/core';
import { Router, Routes, RouterModule } from '@angular/router';
import { ViewstudComponent } from './student/viewstud/viewstud.component';
import { NotFound } from './not-found/not-found.component';
const routes:Routes=[
  {path:"",component:HomeComponent},
  {path:"login",component:LoginComponent},
  {path:"addUpdateFacility",component:AddUpdatefacsComponent,canActivate:[Authguard]},
  {path:"addUpdateCenter",component:AddUpdateComponent,canActivate:[Authguard]},
  {path:"studSignup",component:StudSignupComponent,canActivate:[Authguard]},
  {path:"viewStud",component:ViewstudComponent,canActivate:[Authguard]},
  {path:"updateStud/:id",component:StudSignupComponent,canActivate:[Authguard]},
  {path:"viewSlots",component:ViewSlotsComponent,canActivate:[Authguard]},
  {path:"studslot",component:StudslotComponent,canActivate:[StudGuard]},
  {path:"transactions",component:ViewTransactionsComponent},
  {path:"profile",component:ProfileComponent},
  {path:"**",component:NotFound},
]
@NgModule({
  imports:[RouterModule.forRoot(routes,{scrollPositionRestoration: 'enabled'})],
  exports:[RouterModule],
  providers:[Authguard,StudGuard]
})
export class AppRoutingModule{}
