import { AuthService } from 'src/app/services/auth.service';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material';
import { environment } from 'src/environments/environment';
const BACKEND_URL=environment.apiUrl+"comments";
@Injectable({
  providedIn: 'root'
})
export class CommentService {
  studId=null;
  isAdmin;
  commentsListener=new Subject<any>();
  constructor(private http:HttpClient,private _snackbar:MatSnackBar,
              private authService:AuthService) { }


  getComments(){
    this.authService.getAutoAuth()
    .subscribe(isAdmin=>{
      this.isAdmin=isAdmin;
    })
    this.isAdmin=this.authService.getIsAuth();
    this.studId=localStorage.getItem("studId");
    if(!this.studId) this.studId=null;
    this.http.get(BACKEND_URL,{params:{isValid:this.isAdmin,studId:this.studId}})
   .subscribe((comments:{msg:string,comments:[]})=>{
    this.commentsListener.next(comments.comments);
  })

  }

  getUpdatedComments(){
    return this.commentsListener.asObservable();
  }
  deleteComment(commentId){
    this.http.delete(BACKEND_URL+"/"+commentId)
    .subscribe((isDeleted:{msg:string})=>{
      this.openSnack(isDeleted.msg);
      this.getComments();
    })
  }

  toggleComment(commentId,isValid){
    this.http.put(BACKEND_URL,{id:commentId,isValid:isValid})
    .subscribe(isToggeled=>{
      this.getComments();
      this.openSnack("Comment permission   "+isValid?"approved":"rejected")
    })
  }

  createComment(commentDetails){
    this.http.post(BACKEND_URL,commentDetails)
    .subscribe((isAdded:{msg:string})=>{
      this.openSnack(isAdded.msg)
      this.getComments();
    })
  }

  openSnack(msg){
    this._snackbar.open(msg,"Okay",{
      duration:2000,
      verticalPosition:"top",
      horizontalPosition:"center"
    })
  }
}
