import { MatLabel, MatSnackBar } from "@angular/material";
import { FacilityService } from "./../../../services/facility.service";
import { mimeType } from "./../../add-update/mimeType-validators";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Component, OnDestroy, OnInit } from "@angular/core";
import { StudyCenterService } from "src/app/services/study-center.service";
import { ActivatedRoute, Router } from "@angular/router";
import { HttpErrorResponse } from "@angular/common/http";
import { Subscription } from 'rxjs';

@Component({
  selector: "app-add-updatefacs",
  templateUrl: "./add-updatefacs.component.html",
  styleUrls: ["./add-updatefacs.component.css"],
})
export class AddUpdatefacsComponent implements OnInit,OnDestroy {
  form: FormGroup;
  centerId: String;
  imagePreview: String;
  oldImgPath:String;
  btnName = "Add";
  facId: string;
  addFacSubscription:Subscription;
  paramSubscription:Subscription;

  constructor(
    private studyCenter: StudyCenterService,
    private facService: FacilityService,
    private routerActive: ActivatedRoute,
    private router:Router,
    private _snackBar: MatSnackBar
  ) {}

  ngOnInit() {
    this.form = new FormGroup({
      centerId: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      title: new FormControl(null, {
        validators: [Validators.required],
      }),
      image: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
        asyncValidators: [mimeType],
      }),
      subtitle: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      description: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
    });
    this.getCenter();
    this.paramSubscription=this.routerActive.queryParams.subscribe((param) => {
      this.btnName="Add";
      if (!param._id) return this.form.reset();
      this.btnName = "Update";
      this.imagePreview = param.image;
      this.oldImgPath = param.image;
      this.facId = param._id;
      this.form.patchValue({ ...param });
      this.form.patchValue({ image: null });
    });
  }

  openSnackBar(msg: string) {
    this._snackBar.open(msg, "", {
      duration: 2000,
      horizontalPosition: "center",
      verticalPosition: "top",
    });
  }

  addFacilities() {
    if (this.form.invalid) return console.log(this.form);
    if (this.btnName === "Add") {
      this.facService.addFacilities(this.form.value).subscribe(
        (res: { msg: string }) => {
          this.openSnackBar(res.msg);
        this.facService.getFacilities();
        },
        (error: HttpErrorResponse) => {
          this.openSnackBar(
            "This facility already added select and update if required"
          );
        }
      ) ;
    }
    if (this.btnName === "Update"){
      this.facService.updateFacility(this.form.value, this.facId,this.oldImgPath);
    }
      this.imagePreview='';
      this.form.reset();
      this.router.navigate(['/'],{queryParams:{lebel:"facilities"}});
  }

  getCenter() {
    this.addFacSubscription=this.studyCenter
      .getCenter()
      .subscribe((centerDetails: { result: any[] }) => {
        if (centerDetails.result.length > 0) {
          let fc = centerDetails.result[0];
          this.centerId = fc._id;
          return fc._id;
        }
        else{
          this.openSnackBar("Please add study center details first");
          this.router.navigateByUrl("/addUpdateCenter")
        }
      });
  }
  onImagePicked($event: HTMLInputElement) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file, centerId: this.centerId });
    this.form.get("image").updateValueAndValidity();
    const reader = new FileReader();
    reader.onload = () => {
      this.imagePreview = reader.result as String;
    };
    if (file) reader.readAsDataURL(file);
  }


  ngOnDestroy()
  {
    this.addFacSubscription.unsubscribe();
    this.paramSubscription.unsubscribe();
  }
}
