import { Subscription } from 'rxjs';
import { AuthService } from './../../services/auth.service';
import { ActivatedRoute } from "@angular/router";
import { TransactionService } from "./../../services/transaction.service";
import { AfterViewInit, Component, OnDestroy, OnInit, ViewChild } from "@angular/core";
import {
  MatPaginator,
  MatSort,
  MatTableDataSource,
  PageEvent,
} from "@angular/material";
import { TransactionData } from "src/app/models/transactionModel";

@Component({
  selector: "app-view-transactions",
  templateUrl: "./view-transactions.component.html",
  styleUrls: ["./view-transactions.component.css"],
})
export class ViewTransactionsComponent implements OnInit,OnDestroy {
  isLoading=true;
  displayedColumns: string[] = [
    "tDate",
    "bookedTime",
    "totalFees",
    "paidFees",
    "balance",
  ];
  totalSlots = 0;
  perPage=4;
  pageIndex = 0;
  dataSource: MatTableDataSource<TransactionData["transactions"]>;
  selectedSlot;
  isAdmin=true;
  @ViewChild(MatPaginator, { read: "", static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { read: "", static: false }) sort: MatSort;
  studId:string=null;
  transList = [];
  filteredList: TransactionData[] = [];
  paramsSubscription:Subscription;
  transactionSubscription:Subscription;
  constructor(
    private transactionService: TransactionService,
    private activatedRoute: ActivatedRoute,
    private adminAuthService:AuthService
  ) {}

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  onPageChange(event: PageEvent) {
    this.perPage=event.pageSize;
    this.pageIndex=event.pageIndex;
    this.getTransactions();
  }
  getAll(){
    this.studId=null;
    this.getTransactions();
    this.displayedColumns.length===5 ? this.displayedColumns.unshift('name'):null;
    this.isLoading=false;
  }

  ngOnInit() {
    this.isAdmin=this.adminAuthService.getIsAuth();
    this.paramsSubscription=this.activatedRoute.queryParams.subscribe((obs) => {
      this.selectedSlot = obs;
      this.studId=obs.studOId;
      if(typeof this.studId==='undefined') return this.getAll();
      this.displayedColumns.length===6 ? this.displayedColumns.shift():null;
      this.getTransactions()
    });
  }

  getTransactions(){
    this.transactionService.getStudSlotTransactions({
      slotId: this.selectedSlot.slotId,
      studId: this.studId,
      limit: this.perPage,
      from: this.pageIndex,
    });

    this.transactionSubscription=this.transactionService
      .getUpdatedStudSlotTransactions()
      .subscribe((TransactionData:TransactionData) => {
          this.transList = TransactionData.transactions;
        this.totalSlots=TransactionData.transactionsLen;
        this.dataSource = new MatTableDataSource(this.transList);
        this.isLoading=false;
      });
  }

  ngOnDestroy(){
    this.paramsSubscription.unsubscribe();
    this.transactionSubscription.unsubscribe();
  }
}
