import { MatSnackBar } from '@angular/material';
import { ActivatedRoute, Router } from "@angular/router";
import { StudService } from "./../../services/stud.service";
import { Validators } from "@angular/forms";
import { FormControl } from "@angular/forms";
import { FormGroup } from "@angular/forms";
import { Component, OnInit } from "@angular/core";
import { mimeType } from "src/app/admin/add-update/mimeType-validators";
import { SanckBar } from 'src/app/services/snack-popup.service';
import * as moment from 'moment';

@Component({
  selector: "app-stud-signup",
  templateUrl: "./stud-signup.component.html",
  styleUrls: ["./stud-signup.component.css"],
})
export class StudSignupComponent implements OnInit {
  form: FormGroup;
  startDate=new Date(1990, 0, 1);
  isLoading=true;
  imagePreview: string;
  oldImgPath:string;
  btnName = "Add";
  studID: string;

  dateFilter=(d)=>{
    return d._i.year<2005;
  }

  date = new FormControl(new Date());
  serializedDate = new FormControl((new Date()).toISOString());

  constructor(
    private studService: StudService,
    private route: ActivatedRoute,
    private router:Router,
    private _snackBar:SanckBar
  ) {}

  ngOnInit() {
    this.form = new FormGroup({
      name: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      dob: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(3)],
      }),
      mobile: new FormControl(null, {
        validators: [Validators.required,Validators.pattern("[0-9 ]{10}")],
      }),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType],
      }),
      email: new FormControl(null, { validators: [Validators.required] }),
      password: new FormControl(null, { validators: [Validators.required,Validators.minLength(4)]}),
      qualification: new FormControl(null, {
        validators: [Validators.required],
      }),
      gender: new FormControl(null, { validators: Validators.required }),
      marrageStatus: new FormControl(null, { validators: Validators.required }),
    });

    this.route.queryParamMap.subscribe((res) => {

      this.studID=res.get("id");
      if (!this.studID) return this.isLoading=false;
      this.btnName = "Update";

      this.studService.getStudent(this.studID).subscribe((result:{image,dob}) => {
        this.form.patchValue({ ...result,image: null,dob:null });
        this.oldImgPath=this.imagePreview = result.image;
        this.isLoading=false;
      });
    });
  }

  addStudent() {
    this.isLoading=true;

    if (this.form.invalid) return;
    const formObs=(this.btnName === "Add")
                  ? this.studService.addStudent(this.form.value)
                  :this.studService.updateStudent(this.form.value, this.studID,this.oldImgPath)
      formObs.subscribe((result:{msg:string}) => {
        this.onAddUpdateResult(result);
      },err=>{
        let msg=err.error.errors.email ? "Email id already registered!":"Something Went wrong. Please try again!";
        this.isLoading=false;
        this._snackBar.openSnack(msg);
      });
   }

  onAddUpdateResult(result){
    this.studService.getAllStudents();
    this._snackBar.openSnack(result.msg);
    this.isLoading=false;
    this.form.reset();
    this.router.navigateByUrl("viewStud");
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.form.get("image").updateValueAndValidity();
    const reader = new FileReader();
    reader.onload = () => {
      this.imagePreview = reader.result as string;
    };
    reader.readAsDataURL(file);
  }
}
