import { SlotDetailService } from './../../services/slot-detail.service';
import { Component, OnInit } from '@angular/core';
import { throwError } from 'rxjs';

@Component({
  selector: 'app-studslot',
  templateUrl: './studslot.component.html',
  styleUrls: ['./studslot.component.css']
})
export class StudslotComponent implements OnInit {
  studData;
  selectedSlot;
  isLoading=true;
  constructor(private slotDetailService:SlotDetailService) { }

  ngOnInit() {
    this.slotDetailService.getStudSlot(localStorage.getItem("studId"))
    .subscribe((result:{studId:string})=>{
      this.selectedSlot=result;
      this.studData=result.studId
    })
    this.isLoading=false;
  }

}
